.HeaderBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.trigger {
  font-size: 18px;
  line-height: 50px;
  cursor: pointer;
  margin-left: 16px;
}
.trigger:hover {
  color: #1890ff;
}
.avatar {
  margin-right: 30px;
}
.title-menu {
  background: #f0f7ff;
}
.title-menu .menu-item:hover,
.title-menu .menu-item:focus {
  color: #fff;
  background-color: #4091f7;
}
