.luckysheet-share-logo {
  display: none;
}
#luckysheet_info_detail_title {
  display: none;
}
#chart {
  background-color: white;
  display: none;
  position: absolute;
  z-index: 20;
  width: 100px;
  border: 1px solid #ddd;
  border-radius: 2px;
  box-shadow: 1px 1px 1px #eee;
  padding: 10px 0;
}
#chart ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
#chart ul li {
  text-align: center;
  cursor: pointer;
}
#chart ul li:hover {
  background-color: #eeeeee;
}
.luckysheet-scrollbar-ltr {
  z-index: 20;
}
.luckysheet_info_detail_update {
  display: none;
}
.luckysheet_info_detail_save {
  display: none;
}
