.menu {
  display: none;
  width: 100px;
  z-index: 11;
  border: 1px solid #ececec;
  position: absolute;
  box-shadow: 2px 2px 2px #888888;
  transition: all 0.1s ease;
}
.menu li {
  text-align: center;
  border-bottom: 1px solid #ececec;
  background-color: #fff;
  list-style: none;
  width: 100%;
  opacity: 100%;
}
